/**=====================
    breakpoint mixins css start
==========================**/
body {
  font-family: Rubik, sans-serif;
  position: relative;
  font-size: 14px;
  color: rgb(35, 35, 35);
  margin: 0;
  background-color: #fff;
}
body.landing-body h2 {
  font-size: calc(24px + 8 * (100vw - 320px) / 1600);
}

ul {
  padding-left: 0;
  margin-bottom: 0;
}

li {
  display: inline-block;
  font-size: 14px;
}

p {
  font-size: 14px;
  line-height: 18px;
}

a {
  color: var(--theme-color);
  transition: 0.5s ease;
  text-decoration: none;
}
a:hover {
  text-decoration: none;
  transition: 0.5s ease;
}
a:focus {
  outline: none;
}

.fz-16 {
  font-size: 16px;
}

button:focus {
  outline: none;
}

.btn-close:focus {
  box-shadow: none;
}

:focus {
  outline: none;
}

.form-control {
  background-color: #fff;
}
.form-control:focus {
  box-shadow: none;
}

h1 {
  font-size: calc(40px + 30 * (100vw - 320px) / 1600);
  font-weight: 600;
  line-height: 1.1;
  text-transform: capitalize;
  margin: 0;
}

h2 {
  font-size: calc(22px + 6 * (100vw - 320px) / 1600);
  font-weight: 600;
  line-height: 1;
  text-transform: capitalize;
  margin: 0;
}

h3 {
  font-size: calc(16px + 4 * (100vw - 320px) / 1600);
  font-weight: 500;
  line-height: 1.2;
  margin: 0;
}

h4 {
  font-size: calc(17px + 1 * (100vw - 320px) / 1600);
  line-height: 1.2;
  margin: 0;
  font-weight: 400;
}

h5 {
  font-size: calc(15px + 1 * (100vw - 320px) / 1600);
  line-height: 1.2;
  margin: 0;
  font-weight: 400;
}

h6 {
  font-size: calc(13px + 1 * (100vw - 320px) / 1600);
  line-height: 1.2;
  margin: 0;
  font-weight: 400;
}

span {
  display: inline-block;
}

.theme-color {
  color: var(--theme-color) !important;
}

.theme-bg-color {
  background-color: var(--theme-color);
}

.fw-600 {
  font-weight: 600;
}

