.tap-to-top {
  background-color: var(--theme-color);
  position: fixed;
  bottom: 15px;
  right: -60px;
  border-radius: 5px;
  overflow: hidden;
  width: calc(35px + 10 * (100vw - 320px) / 1600);
  height: calc(35px + 10 * (100vw - 320px) / 1600);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(15px + 3 * (100vw - 320px) / 1600);
  box-shadow: 0 3px 8px rgba(33, 37, 41, 0.35);
  z-index: 3;
  transition: all 0.5s ease;
  cursor: pointer;
  opacity: 0;
}
.tap-to-top::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 0;
  background: #212529;
  top: 0;
  left: 0;
  bottom: 0;
  z-index: -1;
  transition: 0.5s;
}
.tap-to-top:hover::before {
  height: 100%;
}
.tap-to-top.show {
  right: 15px;
  transition: all 0.5s ease;
  opacity: 1;
}
.tap-to-top a {
  color: #fff;
  width: calc(35px + 5 * (100vw - 320px) / 1600);
  height: calc(35px + 5 * (100vw - 320px) / 1600);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(15px + 3 * (100vw - 320px) / 1600);
  z-index: 3;
}
@media (max-width: 575px) {
  .tap-to-top a {
    bottom: 75px;
  }
}

header .main-menu {
